.my-operation-begin {
  width: 100%;
  display: block;
  background: #F6F7FB;
  height: 100%;
  min-width: 1366px;
}
.my-operation-begin .begin-pic {
  display: block;
  margin: 0 auto;
  padding-top: 60px;
}
.my-operation-begin .begin-pic .text-back {
  vertical-align: middle;
  margin-left: 10px;
  font-size: 16px;
}
.my-operation-begin .begin-pic .top {
  width: 100%;
  height: 60px;
  background: #FFFFFF;
  box-shadow: 0 4px 15px rgba(45, 64, 101, 0.08);
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 120.7%;
  color: #2A283D;
  opacity: 0.99;
  position: fixed;
  top: 0;
  left: 0;
  padding: 20px 60px;
  text-align: center;
  z-index: 99;
  display: block;
}
.my-operation-begin .begin-pic .top .img {
  position: absolute;
  left: 56px;
  top: 50%;
  width: 20px;
  height: 22px;
  transform: translate(0, -50%);
}
.my-operation-begin .begin-pic .top .img img {
  display: block;
}
.my-operation-begin .begin-pic .begin-answer {
  display: block;
  width: 50%;
  margin: 60px auto;
  background: #FFFFFF;
  border-radius: 5px;
  text-align: center;
  padding: 35px;
}
.my-operation-begin .begin-pic .begin-answer .title {
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 120.7%;
  color: #2A283D;
  opacity: 0.99;
}
.my-operation-begin .begin-pic .begin-answer .time {
  font-style: normal;
  font-weight: normal;
  font-size: 12px;
  line-height: 120.7%;
  color: #2A283D;
  opacity: 0.3;
  margin: 15px auto;
}
.my-operation-begin .begin-pic .begin-answer .fraction {
  padding-top: 10px;
  font-style: normal;
  font-weight: normal;
  font-size: 12px;
  line-height: 120.7%;
  color: #2A283D;
  opacity: 0.3;
}
.my-operation-begin .begin-pic .begin-answer .text {
  padding: 35px 0;
  border-top: 1px solid #dddddd;
  display: block;
  margin-top: 20px;
}
.my-operation-begin .begin-pic .begin-answer .text .tab {
  width: 46%;
  margin: 0 auto!important;
  display: block;
}
.my-operation-begin .begin-pic .begin-answer .text .tab .gutter-row {
  display: block;
}
.my-operation-begin .begin-pic .begin-answer .text .tab .gutter-row .title1 {
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 205.2%;
  color: #2A283D;
  opacity: 0.99;
  margin-bottom: 0;
}
.my-operation-begin .begin-pic .begin-answer .text .tab .gutter-row .title2 {
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 205.2%;
  color: #2A283D;
  opacity: 0.5;
  margin-bottom: 0;
}
.my-operation-begin .begin-pic .begin-answer .link {
  background: #3BB9FF;
  border: 0.832446px solid #309DE4;
  box-sizing: border-box;
  border-radius: 5.82712px;
  width: 30%;
  margin: 20px auto;
  height: 37px;
  cursor: pointer;
}
.my-operation-begin .begin-pic .begin-answer .link .ant-btn {
  background: #3BB9FF;
  border: none;
  height: 35px;
  line-height: 35px;
}
.my-operation-begin .begin-pic .begin-answer .link .ant-btn a,
.my-operation-begin .begin-pic .begin-answer .link .ant-btn span {
  font-weight: normal;
  font-size: 17.8492px;
  line-height: 120.7%;
  color: #FFFFFF;
}
html {
  overflow-y: auto;
  overflow-x: auto;
}
